import React, { PropTypes, Component } from 'react';
import styles from './Project.css';
import withStyles from '../../decorators/withStyles';
import Overlay from '../Overlay'

@withStyles(styles)
class Project extends Component {

  static propTypes = {
    onClick: PropTypes.func.isRequired
  };

  render() {
    const titleClassName = !this.props.isActive ? 'Project-title' : 'Project-title fadeOutDown';
    const captionClassName = !this.props.isActive ? '' : 'fadeOutDown';

    return (
      <div className="Project">
        <Overlay />
        <div className="Project-caption">
          <h3 className={titleClassName}>{this.props.title}</h3>
          <p className={captionClassName}>{this.props.caption}</p>
        </div>
        <div className="Project-img">
          <img src={this.props.img} />
      </div>
      </div>
    );
  }
}

export default Project;



